<script>
import axios from 'axios'
export default {
  name: 'Navbar',
  data(){
    return{
      login: {
        email: "",
        password: "",
        role: ""
      },
      user: null
    }
  }, 
  created() {
    if(localStorage.getItem("authenticated")){
      var users = localStorage.getItem("authenticated");
      this.user = JSON.parse(users);
      console.log(this.user.role);
    } else if(localStorage.getItem("authenticated") == null){
      this.user = "pengunjung"
    }
  },
  methods: {
    logout(){
      localStorage.removeItem("authenticated");
      this.$router.replace({ name: "Beranda" });
      window.location.reload("true");
    },

    loginMethod() {
      if(this.login.email != "" && this.login.password != "") {
        var mockAcc = JSON.parse(JSON.stringify(this.$parent.mockAccount))
        console.log(this.$parent)
          if(this.login.email == mockAcc.email && this.login.password == mockAcc.password) {
            this.$parent.authenticated = true;

            var user = {email: this.login.email, role: "peserta", authenticated: true};
              
            localStorage.setItem("authenticated", JSON.stringify(user));
            console.log(JSON.stringify(localStorage.getItem("authenticated")))
            this.$router.replace({ name: "Beranda" });
            window.location.reload("true");
          }
          else {
              console.log("The username and / or password is incorrect");
          }
      } else {
          console.log("A username and password must be present");
