<template>
  <div>
    <div v-if="this.checkUser == 'pengunjung'">
        <BerandaUtama/>
    </div>
    <div v-if="this.checkUser.role == 'peserta'">
        <BerandaPeserta/>
    </div>
  </div>
</template>

<script>
import BerandaAdmin from './BerandaAdmin.vue'
import BerandaPeserta from './BerandaPeserta.vue'
import BerandaInstruktur from './BerandaInstruktur.vue'
import BerandaUtama from './BerandaUtama.vue'

export default {
  name: 'Beranda',
  components: {
      BerandaAdmin, 
      BerandaPeserta, 
      BerandaInstruktur, 
      BerandaUtama
  },
  data(){
      return{
        checkUser: null,
      }
  },
  created(){
      if(localStorage.getItem("authenticated")){
          var user = localStorage.getItem("authenticated");
          this.checkUser = JSON.parse(user);
          console.log(this.checkUser.role);
      } else if(localStorage.getItem("authenticated") == null){
          this.checkUser = "pengunjung"
      }
  }
}
</script>

